.message-component{--color:$main-grey;-ms-flex-align:center;align-items:center;color:var(--color);display:-ms-flexbox;display:flex;font-size:.86rem;height:1.36rem;margin-top:.36rem;-webkit-transition:all .15s ease-in-out;transition:all .15s ease-in-out}.message-component svg{fill:var(--color);width:1.14rem}.message-component_error{--color:$main-red}.message-component_success{--color:$main-green}